@import url('https://fonts.googleapis.com/css?family=Caveat');
@import url('https://fonts.googleapis.com/css?family=Staatliches');


$colors: #ff7eb9, #ff65a3, #7afcff,#feff9c, #fff740;
$imgKey: random(5);
$nth: nth($colors, $imgKey);

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


body,
html,
div.board,
div#react-container {
    height: 100%;
    overflow: hidden;
}

div.board {
    background-color: brown;
    width: 100%;
    height: 1000px;
    background: #eab92d;
    background: -moz-radial-gradient(center, ellipse cover, #eab92d 57%, #c79810 99%);
    background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(57%, #eab92d), color-stop(99%, #c79810));
    background: -webkit-radial-gradient(center, ellipse cover, #eab92d 57%, #c79810 99%);
    background: -o-radial-gradient(center, ellipse cover, #eab92d 57%, #c79810 99%);
    background: -ms-radial-gradient(center, ellipse cover, #eab92d 57%, #c79810 99%);
    background: radial-gradient(ellipse at center, #eab92d 57%, #c79810 99%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#eab92d', endColorstr='#c79810', GradientType=1);

    .mainTitle {
      color:#000;
      font-family: 'Staatliches', cursive;
      font-size: 18px;
    }
    button {
        width: 100px;
        height: 50px;
        margin: 20px 0px 0px 20px;
        color: #fff;
        font-size: 20px;
        box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.55);
        border-radius: 20px;
    }
}

div.note {
    background-color: $nth;
    height: 150px;
    width: 150px;
    margin: 2px 0;
    position: absolute;
    cursor: -webkit-grab;
    -webkit-box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, .2);
    box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, .2);

    &:nth-of-type(2n+1) {
        background-color:#ff7eb9;
    }
    &:nth-of-type(4n+1) {
        background-color:#7afcff;
    }

    &:nth-of-type(6n+1){
    background-color: #feff9c;
    }

    &:hover > span {
        opacity: 1;
    }

    &:active {
    cursor: -webkit-grabbing;
    }

    p {
        font-size: 22px;
        padding: 5px;
        font-family: 'Caveat', cursive;
    }

    span {
        position: absolute;
        bottom: 2px;
        right: 2px;
        opacity: 0;
        transition: opacity .25s linear;
    }

    button {
        margin: 2px;
    }

    textarea {
        height: 75%;
        background: rgba(255, 255, 255, .5);
    }

    button {
        width:  30px;
        height: 30px;
        font-size: 14px;
        margin: 0px 0px 0px 20px;
        color: #fff;
        box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.55);
        border-radius: 20px;
    }
}


#add {
  background-color: #5cb85c;
  height: 3em;
}

#edit {
  background-color: #337ab7;
}

#save {
  background-color: #5cb85c;
  width: 100px;
  font-weight: bold;
}
#remove {
  background-color: #d9534f;
}
